import postBox from "../../components/dities/details.vue"
import couponBox from "../../components/coupon/coupon.vue"
import evaluate from "../../components/evaluate/evaluate.vue"
export default {
	components: {
		postBox,
		couponBox,
		evaluate,
	},

	data() {
		return {
			box_el: ['商品列表', '领券', '评论'],
			mislist: 0,
			show1: true,
			show2: false,
			show3: false,
			lastY: 0,
			offY: 0,
			flag: 0,
			HN_height: '',
			HN_top: 300 + 'rpx',
			currentIndex: 0,
			videos: false,
			images: true,
			btnbox: ["图片", "视频"],
			imgbox: [{
				pic: "../../static/images/bar.jpg"
			}, {
				pic: "../../static/images/bar.jpg"
			}, {
				pic: "../../static/images/bar.jpg"
			}, {
				pic: "../../static/images/bar.jpg"
			}],
		}
	},
	methods: {
		ass_pic:function(){
			uni.navigateTo({
				url:'../assemble/assemble'
			})
		},
		seck_pic:function(){
			uni.navigateTo({
				url:'../seckill/seckill'
			})
		},
		changeIndex(index) {
			this.currentIndex = index;
			if (index == 0) {
				this.show1 = true
				this.show2 = false
				this.show3 = false
			}
			if (index == 1) {
				this.show1 = false
				this.show2 = true
				this.show3 = false
			}
			if (index == 2) {
				this.show1 = false
				this.show2 = false
				this.show3 = true
			}
		},
		changeous(index) {
			this.mislist = index
			if (index == 0 && this.videos == false) {
				this.videos = this.videos
				this.images = this.images
			}
			if (index == 0 && this.videos == true) {
				this.videos = !this.videos
				this.images = !this.images
			}
			if (index == 1 && this.videos == false) {
				this.videos = !this.videos
				this.images = !this.images
			}
			if (index == 1 && this.videos == true) {
				this.videos = this.videos
				this.images = this.images
			}
		},
		handletouchstart: function(event) {
			//第一次鼠标触点坐标
			this.lastY = event.changedTouches[0].pageY;
			this.offY = event.currentTarget.offsetTop;
		},
		handletouchmove: function(event) {
			if (this.flag !== 0) {
				return;
			}
			//第二次鼠标触点
			let currentY = event.changedTouches[0].pageY;
			//滑动距离
			let ty = currentY - this.lastY;
			//得到距顶部距离
			let offY = this.offY
			//top值
			this.HN_top = (offY + ty) + 'px'
		},
		handletouchend: function(event) {
			if (event.changedTouches[0].pageY - this.lastY > 0) {
				let _this = this;
				//获取当前窗口高度
				uni.getSystemInfo({
					success: function(res) {
						_this.HN_height = res.windowHeight
						if (parseInt(_this.HN_top) > 0 && parseInt(_this.HN_top) < _this.HN_height / 2) {
							_this.HN_top = 200 + 'rpx'
						} else {
							_this.HN_top = 640 + 'rpx'
						}
					}
				});
				this.flag = 0;
				this.text = '没有滑动'
			}
		}
	}
}
